.popup__input {
  width: 358px;
  height: 32px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  font-size: 14px;
  line-height: 17px;
  font-weight: normal;
  margin-bottom: 5px;
}

@media screen and (max-width: 767px) {
  .popup__input {
    width: 238px;
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 2px;
  }

  .popup__input:first-of-type {
    margin-bottom: 5px;
  }
}
